.fr-skiplinks{--ul-type:none;--ol-type:none;--ul-start:0;--ol-start:0;--xl-block:0;--li-bottom:0;--ol-content:none;position:absolute;padding:1rem 0;transform:translateY(-100%)}.fr-skiplinks:focus-within{position:relative;transform:translateY(0)}.fr-skiplinks__list{display:flex;flex-direction:column;margin:0 -1rem -1.5rem}.fr-skiplinks__list>li{margin:0 1rem 1.5rem}.fr-skiplinks__list>li:before{content:none}.fr-skiplinks__list>li .fr-link{font-size:1rem;line-height:1.5rem;min-height:2rem;padding:.25rem .75rem;border-radius:1rem}.fr-skiplinks__list>li .fr-link:before{font:normal normal normal 1.5rem/1 dsfr-icons;content:none}.fr-skiplinks__list:focus-within{position:relative;transform:translateY(0)}.fr-skiplinks{background-color:var(--g200)}@media (min-width:48em){.fr-skiplinks__list>li{margin:0 1rem}.fr-skiplinks__list{flex-direction:row;flex-wrap:wrap;margin:0 -1rem}}